.nigeriaCurrencyImage {
  background-image: url(./components/img/nigerianFlag.jpg);
  background-size: 100%;
}

.nigeriaCurrencyImageBlur {
  background-image: url(./components/img/nigerianFlag.jpg);
  background-size: 100%;
  opacity: 0.5;
}

.usaCurrencyImage {
  background-image: url(./components/img/usFlag.png);
  background-size: 100% 100%;
}

.usaCurrencyImage2 {
  background-image: url(./components/img/usFlag.png);
  background-size: cover;
}

.pointerScale {
  cursor: zoom-in;
  transform: translateZ(1);
  transition: transform 0.26s ease-out;
}

.pointerScale:hover {
  transform: scale(1.24);
  background-color: rgb(247, 236, 209);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
}

.editDeletePointerScale {
  cursor: pointer;
  transform: translateZ(1);
  transition: transform 0.26s ease-out;
}

.editDeletePointerScale:hover {
  transform: scale(2.24);
}

.logoPointerScale {
  cursor: zoom-in;
  transform: translateZ(1);
  transition: transform 0.26s ease-out;
}

.logoPointerScale:hover {
  transform: scale(2.34);
  margin-top: 1.74rem;
  margin-left: 1.64rem;
}
.logoPointerScale2 {
  cursor: zoom-in;
  transform: translateZ(1);
  transition: transform 0.26s ease-out;
}

.logoPointerScale2:hover {
  transform: scale(3.84);
}

.savedLPO {
  cursor: pointer;
  transform: translateZ(1);
  transition: transform 0.26s ease-out;
}

.savedLPO:hover {
  transform: scale(1.54);
}

/* Hode Div On Smaller Screens */
@media only screen and (max-width: 576px) {
  .hideOnSm {
    display: none;
  }
}
